/**
 * Converts a JavaScript Date string to an Excel date number.
 * @param {string} dateString - The date string in 'YYYY-MM-DD' format.
 * @returns {number} The Excel date number.
 */
export function jsDateToExcelDate(dateString) {
  // Convert the date string to a JavaScript Date object
  const date = new Date(dateString);
  // Convert JavaScript Date to Excel date number
  let excelDate = 25569.0 + (date.getTime() - date.getTimezoneOffset() * 60 * 1000) / (1000 * 60 * 60 * 24);
  return excelDate;
}

/**
 * Converts an Excel date number to a JavaScript Date object.
 * @param {number} excelDate - The Excel date number.
 * @returns {string} The date in 'YYYY-MM-DD' format, or an empty string if input is invalid.
 */
export function excelDateToJSDate(excelDate) {
  // Check if the input is valid
  if (!excelDate || isNaN(excelDate)) {
    return "";
  }
  // Convert Excel date to JavaScript Date
  const utcDate = new Date((excelDate - 25569) * 86400 * 1000);
  // Return the date in 'YYYY-MM-DD' format
  return utcDate.toISOString().split("T")[0];
}

/**
 * Displays a notification message to the user.
 * @param {string} message - The message to display.
 * @param {string} type - The type of notification (e.g., 'success', 'error', 'warning').
 */
export function showNotification(message, type) {
  // Get the notification element
  const notificationEl = document.getElementById("notification");

  // Set the message content
  notificationEl.textContent = message;

  // Set the CSS classes for styling and positioning
  notificationEl.className = `alert alert-${type} position-fixed bottom-0 start-50 translate-middle-x mb-3`;

  // Make the notification visible
  notificationEl.classList.remove("d-none");

  // Hide the notification after 3 seconds
  // eslint-disable-next-line no-undef
  setTimeout(() => {
    notificationEl.classList.add("d-none");
  }, 3000);
}

export async function getDataTable(sheetName, tableName) {
  try {
    const data = await Excel.run(async (context) => {
      // Get the specified sheet
      let sheet = context.workbook.worksheets.getItem(sheetName);
      // Get the specified table
      let table = sheet.tables.getItem(tableName);
      // Load the header row
      let range = table.getRange();
      range.load("values");

      await context.sync();
      return range.values;
    });
    return data;
  } catch (error) {
    showNotification("Error when reading data from" + sheetName + ": " + error.message, "danger");
  }
}

export async function getDataHeader(sheetName, tableName) {
  try {
    const data = await getDataTable(sheetName, tableName);
    return data[0];
  } catch (error) {
    showNotification("Error when reading data from" + sheetName + ": " + error.message, "danger");
  }
}

export async function getIndexByColumnName(sheetName, tableName, columnName) {
  try {
    const data = await getDataTable(sheetName, tableName);
    return data[0].indexOf(columnName);
  } catch (error) {
    return -1;
  }
}

export async function getIndexRowbyTaskId(data, taskId) {
  try {
    const taskIndex = data.findIndex((row) => row[0] === taskId); // Tìm index của task
    return taskIndex;
  } catch (error) {
    return -1;
  }
}
